import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {Box, CssBaseline, Container, Grid, Typography, Link } from '@material-ui/core';
import AppBarr from './AppBarr';
import Drawerr from './Drawerr';



function Copyright() {
  const classes = useStyles();
  return (
      <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright  '}
      <Link color="inherit" href="https://material-ui.com/">
        Nicolas Degregorio
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
      </Typography>
  );
}


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    minHeight: '100vh',
  },
  
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  footer: {
    marginTop: '1rem',
    position: 'fixed',
    bottom: 0,
    left: 0,
    width: '100%',
    padding: '1rem',
    backgroundColor:
      theme.palette.type === 'light' ? theme.palette.grey[200] : theme.palette.grey[800],
  },
}));

export default function Dashboard(props) {
  const { children } = props;
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };


  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBarr
        handleDrawerOpen={handleDrawerOpen}
        open={open}
        section={props.section}
      />
     <Drawerr
      handleDrawerClose={handleDrawerClose}
      open={open}
    />
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              {children}  
              {/* <Router>
                  <Route component={Tickets} path="/tickets" exact/> 
                  <Route component={Prueba} path="/tickets/:id" exact/> 
                  <Route component={Calendar} path="/calendar" exact/>
                  <Route component={Infome} path="/informe" exact/> 
              </Router> */}
            </Grid>
          </Grid>
        </Container>
          <footer className={classes.footer}>
              <Copyright />
          </footer>
        
      </main>
    </div>
  );
}