import React from 'react'
import axios from 'axios'
import {useSelector} from 'react-redux'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import TabsCentered from '../ui/TabsCentered';
import {useParams} from 'react-router-dom'
import Dashboard from '../dashboard/Dashboard'

const useStyles = makeStyles({
    root: {
      minWidth: 275,
    },
    bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
    },
    title: {
      fontSize: 14,
    },
    pos: {
      marginBottom: 12,
    },
  });

const Detalles2 = () => {
    const classes = useStyles();
    const usuarioId = useSelector(store => store.usuario.usuario.data._id)
    const [users, setUsers] = React.useState([])
    const [ticketInfo, setTicketInfo] = React.useState({})
    const [responsible, setResponsible] = React.useState([])
    const [comments, setComments] = React.useState([])
    const {id} = useParams()
    const [ticketId, setTicketId] = React.useState(id)
    React.useEffect(() => {
      const ticketData = async (id) =>{
        try {
          const res = await axios.get(`api/ticket/${id}`)
          const data = res.data
          setTicketInfo(data)
          setResponsible(data.team)
          setComments(data.comments)
        } catch (error) {
          console.log(error)
        }
      }
      const getUsers = async () => {
        try {
             const res = await axios.get('users/users')
             const data = res.data
             setUsers(data)
             console.log(users)
        } catch (error) {
             console.log("ocurrio un error")
             console.log(error)
        }
      }
      ticketData(ticketId)
      getUsers()

    },[])
    return (
      <React.Fragment>
        <Card className={classes.root}>
          <CardContent>
            <TabsCentered
              ticket = {ticketInfo}
              setTicket = {setTicketInfo}
              responsible = {responsible}
              setResponsible = {setResponsible}
              usuarioId = {usuarioId}
              comments = {comments}
              users = {users}
            >
          </TabsCentered>
        </CardContent>
        <CardActions>
         
        </CardActions>
      </Card>
      </React.Fragment>
    );
  }
  const Detalles = () => {
    return (
        <div>
            <Dashboard section={"Detalles del Ticket"}>
                <Detalles2>

                </Detalles2>
            </Dashboard>
        </div>
    )
  }

export default Detalles
