import React, { Fragment } from 'react';
import axios from 'axios'
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import {AppBar, Button, Chip, Dialog, DialogActions, DialogContent, DialogTitle, 
         FormControl,  Tabs, Tab, TextField, Typography, Box, Grid} 
from '@material-ui/core';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import PersonIcon from '@material-ui/icons/Person';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Chat from './Chat';
import moment from 'moment'
import {useSelector} from 'react-redux'
import Notification from './Notification';
import { useForm, Controller } from 'react-hook-form';



function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 400,
  },
}));

export default function ScrollableTabsButtonAuto(props) {
  const {ticket, setTicket,  responsible, setResponsible, usuarioId, comments, users} = props
  const classes = useStyles();
  const {register, errors, handleSubmit, control} = useForm();
  const [notification, setNotification] = React.useState({open: false, message: '', severity: ''});
  const [value, setValue] = React.useState(0);
  const [team, setTeam] = React.useState([])
  const [userTeam, setUserTeam] = React.useState([])
  React.useEffect(() => {
    const getUsersTeam = () => {
      const usersRepeat = []
      responsible.map(user => usersRepeat.push(user._user._id) )
      const arrayFiltrado = users.filter((user) => !usersRepeat.includes(user._id))
      setUserTeam(arrayFiltrado)
    }
    getUsersTeam()
  },[responsible])

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [open, setOpen] = React.useState(false);
 
  function handleInputChange(event, value) {
    setTeam(value);
  }

  const handleClickOpen = () => {
    setOpen(true);
    console.log(userTeam)
  };

  const handleClose = () => {
    setOpen(false);
  };

  const addMembersTeam = async () =>{
    const membersTeam = {
      ticketId: ticket._id,
      members: team
    }
    try {
      const res = await axios.post('api/ticket/team/addMembersTeam', membersTeam) 
      handleClose()
      setResponsible(res.data.team)
      setNotification({...notification, open: true,  message: "Miembro/s Agregados Correctamente", severity: "success"})
    } catch (error) {
      console.log(error)
    }
  }

  const deleteMembersTeam = async (userId) =>{
    const ticketId = ticket._id
    try {
      const res = await axios.delete(`api/ticket/team/deleteMembersTeam?ticketId=${ticketId}&userId=${userId}`)
      const arrayFiltrado = responsible.filter(respon => respon._user._id !== userId ) 
      setResponsible(res.data.team)
      setNotification({...notification, open: true,  message: "Miembro Eliminado Correctamente", severity: "error"})
    } catch (error) {
      console.log(error)
    }
  }

  const ticketSolve = async (data) => {
    const solution = {
      ticketId : ticket._id,
      solution: data.ticketSolution

    }
    try {
      await axios.post('api/ticket/solution/change', solution)
      handleClose()
      setNotification({...notification, open: true,  message: "Situacion del Ticket Informada", severity: "success"})
      setTicket({...ticket,  solution: solution.solution})
    } catch (error) {
      console.log(error)
    }
  }

  

  return (
    <div className={classes.root}>
      <Notification 
                message = {notification.message}
                open = {notification.open}
                severity= {notification.severity}
                setNotification={setNotification}
      />
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="fullWidth"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
          <Tab label="Detalles" {...a11yProps(0)} />
          <Tab label="Equipo" {...a11yProps(1)} />
          <Tab label="Mensajes" {...a11yProps(2)} />
          {/* <Tab label="Item Four" {...a11yProps(3)} />
          <Tab label="Item Five" {...a11yProps(4)} />
          <Tab label="Item Six" {...a11yProps(5)} />
          <Tab label="Item Seven" {...a11yProps(6)} /> */}
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <Grid container>
          <Grid item xs={12}>
            <Box display="flex" justifyContent="flex-end">
              {responsible.map( item => (item.role === 'Leader' && item._user._id === usuarioId && ticket.solution === '') ?
                <Button onClick={handleClickOpen}>Cambiar Estado Ticket</Button>
                : '')}
              {(ticket.solution !== '' && ticket.status === 'Sin Solucionar') &&
                <Chip label="Ticket Pendiente de Autorizacion" variant="outlined" color="primary" />
              }
              {ticket.status === 'Solucionado' &&
                <Chip label="Ticket Resuelto" variant="outlined"  icon={<CheckIcon />} color="default" style={{backgroundColor:'#8bc34a'}} />
              }
            </Box> 
            <Box>
              <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
                <DialogTitle>Finalizar Ticket</DialogTitle>
                <DialogContent>
                  <form  onSubmit={handleSubmit(ticketSolve)} className={classes.root}className={classes.container}>
                    <FormControl className={classes.formControl}>
                      <Controller
                          rules={{
                                  required: {
                                      value: true, 
                                      message: 'Campo requerido'
                                      }, 
                                  minLength: {
                                      value: 6, 
                                      message: 'Mnimo 6 carcteres'
                                  }
                                  
                          }}
                          control={control}
                          name="ticketSolution" 
                          id="ticketSolution"
                          label="Que se Hizo"
                          variant="outlined" 
                          defaultValue={''}
                          
                          as={
                          <TextField
                              error={ errors.ticketSolution ? true : false }
                              helperText={errors.ticketSolution ? errors.ticketSolution.message : null} 
                              
                          />
                      }
                      />
                    </FormControl>
                  </form>
                </DialogContent>
                <DialogActions>
                  <Button onClick={handleClose} color="primary">
                    Cancelar
                  </Button>
                  <Button onClick={handleSubmit(ticketSolve)} color="primary">
                    Ok
                  </Button>
                </DialogActions>
              </Dialog>   
            </Box>     
          </Grid>
            <Grid item xs={12} md={6} >
                <Box>
                    <Typography variant="h6" gutterBottom>
                    Descripcion
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        {ticket.description}
                    </Typography>
                </Box>
            </Grid>
            <Grid item xs={12} md={6}>
                <Box>
                    <Typography variant="h6" gutterBottom>
                        Fechas
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        Fecha Inicio: {moment(ticket.start_date,'YYYY-MM-DD').format('DD-MM-YYYY')}
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                        Fecha Vencimiento: {moment(ticket.end_date,'YYYY-MM-DD').format('DD-MM-YYYY')}
                    </Typography>
                </Box>
            </Grid>
        </Grid>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Grid container>
          <Grid md={12}>
            <Box display="flex" justifyContent="flex-end">
              {responsible.map( item => (item.role === 'Leader' && item._user._id === usuarioId && ticket.solution === '') ?
              <Button onClick={handleClickOpen}>Agregar al Equipo</Button>
              :'')}
                  <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
                    <DialogTitle>Agregar Integrantes</DialogTitle>
                    <DialogContent>
                      <form className={classes.container}>
                        <FormControl className={classes.formControl}>
                          <Autocomplete
                            multiple
                            id="tags-standard"
                            options={userTeam}
                            getOptionLabel={(option) => option.nombre}
                            onChange={handleInputChange}
                            renderInput={(params) => (
                              <TextField
                                {...params}
                                variant="standard"
                                label="Integrantes"
                                placeholder="Usuarios"
                              />
                            )}
                          />
                        </FormControl>
                      </form>
                    </DialogContent>
                    <DialogActions>
                      <Button onClick={handleClose} color="primary">
                        Cancelar
                      </Button>
                      <Button onClick={addMembersTeam} color="primary">
                        Ok
                      </Button>
                    </DialogActions>
                  </Dialog>  
              </Box>  
          </Grid>
        </Grid>
        <Grid container spacing={-2}>
          <Grid md={6}>
                <Box>
                    <Typography variant="h6" gutterBottom>
                        Encargado:
                    </Typography>
                      <List dense>
                        {responsible.map( item => 
                          item.role === 'Leader'? 
                          <Fragment>
                              <ListItem>
                                <ListItemAvatar>
                                  <Avatar>
                                    <PersonIcon />
                                  </Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                  primary={item._user.nombre}
                                  secondary={null}
                                />
                              </ListItem>
                            </Fragment>
                            : ''
                        )}
                      </List>
                </Box>
            </Grid>
            <Grid md={6}>
                <Box>
                    <Typography variant="h6" gutterBottom>
                        Equipo de Soporte:
                    </Typography>
                    <List dense>
                      {responsible.map( item => 
                          item.role === 'Support' ?
                          <Fragment>
                            <ListItem>
                              <ListItemAvatar>
                                <Avatar>
                                  <PersonIcon />
                                </Avatar>
                              </ListItemAvatar>
                              <ListItemText
                                primary={item._user.nombre}
                                secondary={null}
                              />
                            {ticket.team[0]._user._id === usuarioId &&  
                              <ListItemSecondaryAction>
                                <IconButton edge="start" aria-label="delete" onClick={() => deleteMembersTeam(item._user._id)}>
                                  <DeleteIcon />
                                </IconButton>
                              </ListItemSecondaryAction>
                            }
                            </ListItem>
                          </Fragment>
                          : ''
                      )}
                    </List>
                </Box>
            </Grid>
        </Grid>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Chat
          ticket = {ticket}
          userId = {usuarioId}
          comments = {comments}
        >
              
        </Chat>
      </TabPanel>
      {/* <TabPanel value={value} index={3}>
        Item Four
      </TabPanel>
      <TabPanel value={value} index={4}>
        Item Five
      </TabPanel>
      <TabPanel value={value} index={5}>
        Item Six
      </TabPanel>
      <TabPanel value={value} index={6}>
        Item Seven
      </TabPanel> */} 
    </div>
  );
}