import React, {Fragment, useState, useEffect} from 'react'
import axios from 'axios'
import { Box, Button, Grid} from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import MaterialTable from 'material-table'
import FormAddEdit from './Forms/Add-Edit'
import Popup from '../../ui/Popup'
import Notification from '../../ui/Notification'
import Dashboard from '../../dashboard/Dashboard'

const Users2 = () => {
    const [openPopup, setOpenPopup] = useState(false);
    const [notification, setNotification] = React.useState({open: false, message: ''});
    const [user, setUser] = useState({
        name: '',
        lastName: '',
        role: '',
        email: '',
    });
    const [users, setUsers] = useState([])
    const [edit, setEdit] = useState(false);
    const [id, setId] = useState('');

    useEffect(() => {
        const getUsers = async () =>{
           const res =  await axios.get('users/users')
           setUsers(res.data)
        }
        getUsers();
    },[])

    const addUser = async (data) => {
        const newUser = {
            name: data.userName,
            lastName: data.userLastName,
            email: data.userEmail,
            password: data.userPassword,
            role: data.userRole
        }
        try {
            const res = await axios.post('users/users', newUser)
            setUsers([
                ...users,
                res.data
            ])
            setNotification({...notification, open: true,  message: "Usuario Creado Correctamente"})
            emptyFields()
        } catch (error) {
            console.log(error)
        }
    }

    const edition = (row) => {
        setId(row._id)
        setUser({
            name: row.nombre,
            lastName: row.lastName,
            email: row.email,
            role: row.role
        })
        setEdit(true)
        setOpenPopup(true)
        console.log(user)
    }

    // Actualizar Usuario
    const editUser =  async (data) => {
        const userEdit = {
            name: data.userName,
            lastName: data.userLastName,
            email: data.userEmail,
            password: data.userPassword,
            role: data.userRole
        }
        try {
            const res = await axios.put(`users/users/${id}`,userEdit)
            const arrayEditado = users.map( user => (
                user._id === id ? res.data : user
            ))
            console.log(res.data)
            setUsers(arrayEditado)
            setNotification({...notification, open: true, message: "Usuario Editado Correctamente"})
            emptyFields()
        } catch (error) {
            console.log(error)
        }
    }

    const deleteUser = async data => {
        const usersIds = []
        data.map(user => usersIds.push(user._id))
        const confirm = window.confirm("Estas Seguro de Eliminar El/Los Registro/s")
        try {
            if(confirm){
                const res = await axios.post('users/users/delete/many', usersIds)
                const arrayFiltrado = users.filter((user) => !usersIds.includes(user._id))
                setUsers(arrayFiltrado)
                setNotification({...notification, open: true,  message: "Usuario/s Eliminado/s Correctamente"})
            }     
        } catch (error) {
            console.log(error)
        }
    }

    // Cerrar formulario flotante
    const closeForm = () => {
        setOpenPopup(false)
        setEdit(false)
    }
    // Devuelve el estado del Ticket a situacion inicial
    const emptyFields = () =>{
        closeForm()
        setUser({
            name: '',
            lastName: '',
            role: '',
            email: ''
        })
        setId('')

    }
    return (
        <Fragment>
            <Grid container spacing={1}>
                <Grid item sm={12}> 
                    <Box>
                        <Button variant="contained" color="primary" onClick={() =>  setOpenPopup(true)}>
                            Nuevo Usuario
                        </Button>
                    </Box>
                </Grid>
                <Grid item sm={12}>
                    <Box mt={2}>
                        <MaterialTable
                            columns={[
                                { title: 'Id', field: '_id', hidden: true},
                                { title: 'Nombre', field: 'nombre' },
                                { title: 'Apellido', field: 'lastName' },
                                { title: "role", field: 'role'},
                                { title: 'email', field: 'email' },
                            ]}
                            data={users}
                            actions={[
                                {
                                  //hidden: roleUser !== 'Admin',
                                    icon: 'edit',
                                    tooltip: 'Editar',
                                    position: 'row',
                                    onClick: (event, rowData) => edition(rowData),
                                },
                                {
                                    icon: () => <DeleteIcon />,
                                    position: 'toolbarOnSelect',
                                    tooltip: 'Borrar',
                                    onClick: (event, rowData) => deleteUser(rowData),
                                }
                              ]}
                            options={{
                                actionsColumnIndex: -1,
                                selection: true,
                                pageSize: 10
                            }}
                            title="Usuarios"
                        />
                    </Box>
                </Grid>
            </Grid>

            <Notification 
                    message = {notification.message}
                    open = {notification.open}
                    severity= {"success"}
                    setNotification={setNotification}
            />
            <Popup
               openPopup={openPopup}  
               title= {edit ? "Editar Usuario" : "Agregar Usuario"} 
               closeForm={closeForm}           
            >
               <FormAddEdit
                edit={edit}
                user={user}
                addUser = {addUser}
                editUser = { editUser} 
                emptyFields={emptyFields}  
                />
            </Popup>
        </Fragment>
    )
}

const Users = () => {
    return (
        <div>
            <Dashboard section={"Usuarios"}>
                <Users2>

                </Users2>
            </Dashboard>
        </div>
    )
  }

export default Users
